}*/

body {
  margin: 0;
}

/*main {
  /*z-index:-1;
}*/

.page {
  height: 100vh;
  margin-left: 300px;
}

.navbar {
  display: flex;
  height: 100vh;
  width: 300px;
  position: fixed;
  background-color: #332f3f;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-y: scroll;
  -ms-overflow-style: none;  /* IE and Edge */  /* Hide scrollbar for IE, Edge and Firefox */
  scrollbar-width: none;  /* Firefox */  
  border-right:1px solid; 
}

/* Hide scrollbar for Chrome, Safari and Opera */
.navbar::-webkit-scrollbar {
  display: none;
}


.navbar ul {
  list-style-type: none;
  color: white;
  font-size: 25px;
}

.navbar ul li {
  cursor: pointer;
  padding:10px;
}

.navbar li:hover {
  color: #0945b4;
}


.tech {
  margin-right:15vw;
  width: 200px;
  height: 100px;
}

.barTech {
  display: flex;
  justify-content: center; /* align items horizontally */
  width: 100%;
  height: 100%;
  margin-left:300px;
  background-color: #332F3F;
  border-top: 1px solid;
  border-bottom: 1px solid;

}

.navbar li img{
  
  display: inline-block;
  margin-top:10px;
  margin-bottom:-10px;
  width: 35px;
  height: 35px;
  margin-right: 10px;
}

.navbar ul li.active {
  font-weight: bold;
  color: #0945b4;
}

